*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Verdana, Geneva, Tahoma, sans-serif;
}

.app{
  height: 100%;
  width: 100vh;
  background-position: center;
  background-size: cover;

}
.overlay{
  height: 100%;
  width: 100vh;

}
.container{
  max-width: 800px;
  height: 100%;
  margin: auto;
  display: flex;
  align-items: center;
  justify-content: space-between;
  flex-direction: column;
  padding: 1rem;
}
.section{
  width: 100%;
  border-radius: 0.4rem;
  padding: 1rem;
  background-color: rgba(0,0,0,0.5); 

}
.section_inputs,.section_temprature{
  display: flex;
  align-items: center;
  justify-content: space-between;
margin-bottom: 80px;
}
.section_inputs>input{
  border: 1px solid whitesmoke;
  border-radius: 0.4rem;
  padding: 0.5rem;
  background-color: transparent;
  font-size: 20px;
  font-weight: 200;


}

.section_inputs>input:focus{
  outline: none;

}
button {
  background-color: white;
  font-size: 14px;
  padding: 10px 50px;
  border: none;
  border-radius: 00.4rem;
  text-decoration: none;
  transition: 0.3s background-color;
  font-size: 20px;
  font-weight: 200;
 cursor: pointer;
}

button:hover {
  background-color: gray;
}
.icon{
  display: flex;
  align-items: center;
  justify-content: center;
 flex-direction: column;

}
.icon>h3{
  font-size: 20px;
  font-weight: 200;
 text-transform: capitalize;
}
.temprature>h1{
  font-size: 60px;

}
.sec{
  background-color: rgba(0,0,0,0.5); 


}
.section-descriptions{
  display: grid;
  grid-template-columns: repeat(3,1fr);
  gap: 1rem;
}
.card{
display: flex;
flex-direction: column;
align-items: center;
justify-content: space-between;
padding: 1rem;
border-radius: 0.4rem;
background-color: rgba(0,0,0,0.5); 

}
.description-card{
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  gap: 5rem;
  }
  .description-card>small{
      text-transform: capitalize;
      }
      @media screen and (max-width:1024px) {
        .section-descriptions{
          grid-template-columns: 1fr 1fr;
        }
        
      }